<template>
  <div class="undownload">
    <unselect ref="unselect"/>
    <el-button @click="getData">
      下载数据
    </el-button>
  </div>
</template>

<script>
import unselect from "@com/unselect/index"
export default {
  name: "Undownload",
  components: {unselect},
  data() {
    return {
      param: {
        freq: "A",
        px: "HS",
        cc: [],
        r: [],
        p: [],
        start: "",
        end: "",
        rg: ["ALL"],
        type: "C"
      },
    }
  },

  computed: {

  },

  mounted() {
  },

  methods: {
    getData() {
      let data = this.$refs["unselect"].getParam();
      if (!data) return;
      data.fmt = "csv";
      let url = "?";
      this.$jc.forIn(data, (key,val, i) => {
        url +=  `${(i == 0 ? "" : "&")}${key}=${val}`
      })

      window.open(this.$url.comtrade.csv + url);
      // this.$get(this.$url.comtrade.data, {
      //   data: data
      // })
    }
  }
}
</script>

<style lang="scss">
.undownload {
	height: 100%;
	width: 100%;
}
</style>
