<template>
  <div class="unselect">
    <el-form
      ref="form"
      :model="param"
      :inline="true"
      :rules="rules"
    >
      <el-form-item
        label="贸易类型:"
        prop="type"
      >
        <el-select v-model="param.type">
          <el-option
            label="Commodities"
            value="C"
          >
            <span style="float: left;">Commodities</span>
            <span style="float: right; color: #8492a6; font-size: 13px;">商品</span>
          </el-option>
          <el-option
            label="Services"
            value="S"
          >
            <span style="float: left;">Services</span>
            <span style="float: right; color: #8492a6; font-size: 13px;">服务</span>
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item
        label="时间间隔:"
        prop="freq"
      >
        <el-select
          v-model="param.freq"
          @change="freqChange"
        >
          <el-option
            label="Annual"
            value="A"
          >
            <span style="float: left;">Annual</span>
            <span style="float: right; color: #8492a6; font-size: 13px;">年</span>
          </el-option>
          <el-option
            label="Monthly"
            value="M"
          >
            <span style="float: left;">Monthly</span>
            <span style="float: right; color: #8492a6; font-size: 13px;">月</span>
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item
        label="时间选择:"
        prop="start"
        required
      >
        <el-date-picker
          v-model="param.start"
          :type="param.freq == 'M' ? 'month' : 'year'"
          :value-format="param.freq == 'M' ? 'yyyyMM' : 'yyyy'"
          placeholder="选择开始时间"
        />
        至
        <el-date-picker
          v-model="param.end"
          :type="param.freq == 'M' ? 'month' : 'year'"
          :value-format="param.freq == 'M' ? 'yyyyMM' : 'yyyy'"
          placeholder="选择结束时间"
        />
      </el-form-item>
      <div/>
      <el-form-item
        label="报告区域:"
        prop="r"
      >
        <el-select
          v-model="param.r"
          filterable
          multiple
          :multiple-limit="5"
          collapse-tags
        >
          <el-option
            v-for="item in partnerAreas.results"
            :key="item.id"
            :label="item.text"
            :value="item.id"
          />
        </el-select>
      </el-form-item>
      <el-form-item
        label="伙伴区域:"
        prop="p"
      >
        <el-select
          v-model="param.p"
          filterable
          multiple
          :multiple-limit="5"
          collapse-tags
        >
          <el-option
            v-for="item in partnerAreas.results"
            :key="item.id"
            :label="item.text"
            :value="item.id"
          />
        </el-select>
      </el-form-item>
      <el-form-item
        label="编码分类:"
        prop="cc"
      >
        <el-select
          v-model="param.px"
          :disabled="param.freq == 'M'"
          @change="classificationChange"
        >
          <el-option
            label="HS"
            value="HS"
          >
            <span style="float: left;">HS</span>
            <span style="float: right; color: #8492a6; font-size: 13px;">海关HS编码</span>
          </el-option>
          <el-option
            label="ST"
            value="ST"
          >
            <span style="float: left;">SITC</span>
            <span style="float: right; color: #8492a6; font-size: 13px;">标准国际贸易分类</span>
          </el-option>
          <el-option
            label="BEC"
            value="BEC"
          >
            <span style="float: left;">BEC</span>
            <span style="float: right; color: #8492a6; font-size: 13px;">广义经济商品分类</span>
          </el-option>
        </el-select>
        <el-select
          v-model="param.cc"
          popper-class="unselect-max-popper"
          :disabled="!param.px"
          filterable
          remote
          multiple
          collapse-tags
          :remote-method="pxRemote"
          :loading="classificationLoading"
          placeholder="请输入至少两个字"
        >
          <el-option
            v-for="item in classification"
            :key="item.id"
            :label="item.text"
            :value="item.id"
          />
        </el-select>
        <el-tooltip
          effect="light"
        >
          <div
            slot="content"
            v-html="pxString"
          />
          <i class="el-icon-info"/>
        </el-tooltip>
      </el-form-item>
      <el-form-item
        label="贸易流向:"
        prop="rg"
      >
        <el-select
          v-model="param.rg"
          filterable
          multiple
          collapse-tags
        >
          <el-option
            v-for="item in tradeRegimes.results"
            :key="item.id"
            :label="item.text"
            :value="item.id"
          />
        </el-select>
      </el-form-item>
    </el-form>
  </div>
</template>

<script type="text/ecmascript-6">
import HS from "./classificationHS";
import ST from "./classificationST";
import BEC from "./classificationBEC";
import partnerAreas from "./partnerAreas";
import tradeRegimes from "./tradeRegimes";
export default {
  name: "Unselect",
  components: {},
  data() {
    var checkTime = (rule, value, callback) => {
        if (!this.param.start || ! this.param.end) {
          return callback(new Error('请选择时间'));
        } else if (this.param.start - this.param.end > 0) {
          return callback(new Error('开始时间不能大于结束时间'));
        } else {
          callback();
        }
      };
    return {
      param: {
        max: 250000,
        freq: "A",
        px: "HS",
        cc: [],
        r: [],
        p: [],
        start: "",
        end: "",
        rg: ["ALL"],
        type: "C"
      },
      rules: {
        start: [
          { validator: checkTime, trigger: 'blur' }
        ],
        freq: [
          { required: true, message: '请选择', trigger: 'blur' },
        ],
        cc: [
          { required: true, message: '请选择', trigger: 'change', type: 'array' },
        ],
        type: [
          { required: true, message: '请选择', trigger: 'blur' },
        ],
        rg: [
          { required: true, message: '请选择', trigger: 'change', type: 'array' },
        ],
        r: [
          { required: true, message: '请选择', trigger: 'change', type: 'array' },
        ],
        p: [
          { required: true, message: '请选择', trigger: 'change', type: 'array' },
        ],
      },
      classificationLoading: false,
      classification: [],
      partnerAreas: partnerAreas,
      tradeRegimes: tradeRegimes,
    }
  },

  computed: {
    pxString() {
      let str = this.param.cc.length > 0 ?  "选中的值:<br>" + this.param.cc.join("<br>") : "未选择";
      return str
    },
  },

  mounted() {
  },

  methods: {
    freqChange(data) {
      if (data == "M") {
        const h = this.$createElement;
        this.param.px = "HS";
        this.param.cc = [];
        this.classification = [];
        this.$notify({
          title: '提示',
          message: h('i', { style: 'color: teal'}, '每月数据集可以混合来自多个HS修订版的代码，并且，除了贸易流和伙伴区域信息的标准化以及转换为美元外按原样提供。')
        });
      }
    },
    classificationChange() {
      this.classification = [];
      this.param.cc = [];
    },
    pxRemote(query) {
      console.log(query);
      this.classificationLoading = true;
      let data
      switch (this.param.px) {
        case "HS":
          data = HS;
          break;

        case "ST":
          data = ST;
          break;

        default:
          data = BEC;
          break;
      }

      if (query !== "" && query.length >= 2) {
        let classification = data.results.filter(item => {
          return item.id.indexOf(query) != -1;
        })
        this.classification = classification.sort(function(a,b) {
          return a.id - b.id
        })
      } else {
        this.classification = [];
      }
      this.classificationLoading = false;
    },
    getParam() {
      let param
      this.$refs["form"].validate((valid) => {
        if (valid) {
          param = this.$jc.extend(true, {}, this.param);
          let ps = []
          for (let i = param.start; i <= param.end; i++) {
            ps.push(i);
          }
          param.ps = ps;
          this.$jc.forIn(param, (key, val) => {
            if (this.$jc.isArray(val)) {
              param[key] = val.join(",");
            }
          })
          delete param.start;
          delete param.end;
        } else {
          console.log('error submit!!');
          return false;
        }
      });
      return param
    }
  }
}
</script>

<style lang="scss">
.unselect {
	.el-form {
		text-align: left;
	}

	.el-select + .el-select,
	.el-select + .el-date-editor {
		margin-left: 10px;
	}

	.el-select__tags {
		.el-select__input {
			display: inline-block;
			flex-grow: 0 !important;
			width: 30px !important;
		}
	}

	.el-select__tags-text {
		display: inline-block;
		vertical-align: top;
		max-width: 70px;

		@include omg();
	}
}

.unselect-max-popper {
	max-width: 300px;
	overflow: hidden;
}
</style>
